require('normalize.css/normalize.css');

import React from 'react';
import {Link} from 'react-router';
import Container from './components/Container/Container';
import Button from './components/Button/Button';
import Badge from './components/Badge/Badge';

class ButtonMain extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <Container className="button-container aui-content-padded">
                <p></p>
                <p><button>(default)</button></p>
                <p><button className="aui-btn-primary" onClick={this.handleClick.bind(this)}>(primary)</button></p>
                <p><button className="aui-btn-success">(success)</button></p>
                <p><button className="aui-btn-info">(info)</button></p>
                <p><button className="aui-btn-warning">(warning)</button></p>
                <p><button className="aui-btn-danger">(danger)</button></p>

                <p><button className="aui-btn-info"><span className="aui-iconfont aui-icon-roundcheck"></span></button></p>
                <p><button className="aui-btn-info"><Badge className="aui-badge-warning">12</Badge></button></p>
                <p></p>
                <p><button className="aui-btn-block">(default)</button></p>
                <p><button className="aui-btn-primary aui-btn-block">(primary)</button></p>
                <p><button className="aui-btn-success aui-btn-block">(success)</button></p>
                <p><button className="aui-btn-info aui-btn-block">(info)</button></p>
                <p><button className="aui-btn-warning aui-btn-block">(warning)</button></p>
                <p><button className="aui-btn-danger aui-btn-block">(danger)</button></p>
                <p> - 10px</p>
                <p><button className="aui-btn-block aui-btn-outlined">(default)</button></p>
                <p><button className="aui-btn-primary aui-btn-block aui-btn-outlined">(primary)</button></p>
                <p><button className="aui-btn-success aui-btn-block aui-btn-outlined">(success)</button></p>
                <p><button className="aui-btn-info aui-btn-block aui-btn-outlined">(info)</button></p>
                <p><button className="aui-btn-warning aui-btn-block aui-btn-outlined">(warning)</button></p>
                <p><button className="aui-btn-danger aui-btn-block aui-btn-outlined">(danger)</button></p>
            </Container>
        );
    }

    handleClick() {
        alert(666);
    }
}

export default ButtonMain;



